body {
  height: 100vh;
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  width: 100%;
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  align-items: center;
  margin: 20px auto;
  padding: 20px;
  display: flex;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

h1 {
  color: #333;
  text-align: center;
  margin-bottom: 20px;
  font-size: 2.5rem;
}

.breed-select {
  width: 100%;
  max-width: 400px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 16px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.hidden {
  display: none;
}

.loader {
  text-align: center;
  color: #555;
  margin-top: 20px;
  font-size: 18px;
}

.error {
  color: red;
  text-align: center;
  margin-top: 20px;
  font-size: 18px;
}

.cat-info {
  width: 100%;
  align-items: flex-start;
  gap: 20px;
  margin-top: 20px;
  display: flex;
}

.cat-info img {
  max-width: 300px;
  height: auto;
  object-fit: cover;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, .1);
}

.cat-info-item {
  flex-grow: 1;
}

.cat-info h2 {
  color: #333;
  margin-bottom: 10px;
  font-size: 24px;
}

.cat-info p {
  color: #555;
  margin-bottom: 10px;
  font-size: 16px;
  line-height: 1.5;
}

.cat-info-item {
  flex-direction: column;
  display: flex;
}

